<template>
  <el-tooltip effect="dark" :content="active ? '退出全屏' : '全屏'" placement="bottom">
    <el-button class="techo-mr btn-text can-hover" type="text" @click="toggle">
      <techo-icon v-if="active" name="compress" />
      <techo-icon v-else name="arrows-alt" style="font-size: 16px" />
    </el-button>
  </el-tooltip>
</template>

<script>
  import elementResizeEvent from "element-resize-event"
  import {
    mapState,
    mapMutations
  } from 'vuex'

  export default {
    computed: {
      ...mapState('techo/fullscreen', [
        'active'
      ])
    },
    methods: {
      ...mapMutations('techo/fullscreen', [
        'toggle'
      ])
    }
  }
</script>